<template>
  <div class="post_comment">
    <van-field
      class="comment_textarea"
      v-model.trim="commentWord"
      border
      maxlength="50"
      rows="2"
      autosize
      type="textarea"
      placeholder="请输入评论"
      show-word-limit
    />
    <van-button
      @click="writeComment"
      type="info"
      size="small"
      class="sub_btn"
      :disabled="!commentWord"
      >发表评论</van-button
    >
  </div>
</template>

<script>
import { postArticleComment } from '@/api/article.js';

export default {
  name: 'PostComment',
  props: {
    // 评论的目标id（评论文章即为文章id，对评论进行回复则为评论id）
    target: {
      type: [String, Number, Object],
      required: true,
    },
    // 文章id，对评论内容发表回复时，需要传递此参数，
    // 表明所属文章id。对文章进行评论，不要传此参数。
    articleId: {
      type: [String, Number, Object],
      default: null,
    },
  },
  data() {
    return {
      commentWord: '', // 评论内容
    };
  },
  methods: {
    // 提交评论
    async writeComment() {
      this.$toast.loading({
        message: '评论中...',
        forbidClick: true,
      });
      try {
        const { data } = await postArticleComment({
          target: this.target.toString(),
          content: this.commentWord,
          art_id: this.articleId ? this.articleId.toString() : null,
        });
        console.log(data);
        this.$toast.success('评论成功');
        this.commentWord = '';
        this.$emit('postSuccess', data.data.new_obj);
      } catch (e) {
        console.log(e);
        this.$toast.fail('评论失败');
      }
    },
  },
  created() {},
};
</script>

<style lang="less" scoped>
.post_comment {
  display: flex;
  align-items: flex-end;
  .comment_textarea {
    border: 1px solid #ccc;
  }
  .sub_btn {
    width: 80px;
  }
}
</style>
